import React, { useCallback, useEffect, useState } from "react";
import { data } from "pages/news/data";
import { CategoryType, postCategories, PostType } from "types/post";
import { Link } from "react-router-dom";
import { getLinkPrefix } from "utils/get-link-prefix";
import { ArrowRightColored } from "./ArrowRightColored";
import styles from "./styles.module.scss";

const CategoryButton: React.FC<{
    category: CategoryType;
    onClick: (e: React.MouseEvent<HTMLButtonElement>) => void;
    isActive: boolean;
}> = ({ category, onClick, isActive }) => {
    return (
        <button
            type="button"
            key={category}
            value={category}
            onClick={onClick}
            className={isActive ? styles.activeCategory : ""}
        >
            {category}
        </button>
    );
};

const initialState: Array<PostType> = data;

export const Aside = () => {
    const [state, setState] = useState<Array<PostType>>(initialState);
    const [currentCategory, setCurrentCategory] = useState<
        CategoryType | string
    >("ALL POSTS");

    useEffect(() => {
        if (currentCategory === "ALL POSTS") {
            setState(initialState);
        } else {
            const filteredData = initialState.filter((item) => {
                return item.category === currentCategory;
            });
            setState(filteredData);
        }
    }, [currentCategory, setState]);

    const handleChange = useCallback(
        (e: React.MouseEvent<HTMLButtonElement>) => {
            let category = e.currentTarget;
            setCurrentCategory(category.value);
        },
        [setCurrentCategory]
    );

    return (
        <aside className={styles.aside}>
            <div className={styles.asideSticky}>
                <p className={styles.asideTitle}>blog articles on Kobogo</p>
                <ul className={styles.postsCategories}>
                    {postCategories.map((category, i) => (
                        <li key={category}>
                            <CategoryButton
                                category={category}
                                onClick={handleChange}
                                isActive={currentCategory === category}
                            />
                        </li>
                    ))}
                </ul>
                <ul className={styles.postsLinks}>
                    {state.map((post) => (
                        <li key={post.slug}>
                            <Link
                                to={`${getLinkPrefix(post.category)}/${
                                    post.slug
                                }`}
                            >
                                <span>{post.title}</span>
                                <ArrowRightColored />
                            </Link>
                        </li>
                    ))}
                </ul>
            </div>
        </aside>
    );
};
